import axios from "axios"

const URL = 'https://60b56575fe923b0017c84002.mockapi.io/Products'

const getProducts = async () => {
  try {
    let {data} = await axios.get(URL)
    return data
  } catch(error) {
    throw error
  }
}

const createProduct = async(newProduct) => {
  try {
    let headers = {
      "Content-Type": "application/json"
    }
    let {data} = await axios.post(URL, newProduct, {headers})
    return data
  } catch (error) {
    throw error
  }
}

const deleteProduct = async(id) => {
  console.log(id.id)
  try {
    console.log(`${URL}/${id}`)
    let {data} = await axios.delete(`${URL}/${id}`)
    return data
  } catch (error) {
    throw error
  }
}

export {
  getProducts,
  createProduct,
  deleteProduct,
}